.docker_menu {
  position: fixed;
  bottom: -100%;
  left: 50%;
  transform: translateX(-50%);
  max-width: 576px;
  width: 100%;
  z-index: 100;
  transition: bottom 1s;
}
.docker_menu.fixed {
  bottom: 0;
  transition: bottom 0.5s;
}
.docker_menu_list {
  display: flex;
}
.docker_menu_list::before {
  position: absolute;
  top: 0;
  left: 50%;
  display: block;
  width: 136px;
  height: 74px;
  background-size: 136px 74px;
