}
.docker_menu_item:not(.my_page) {
  border-top: 1px solid #ccc;
  background-color: #fff;
}
.docker_menu_item.my_page {
  position: relative;
  text-align: center;
  max-width: 136px;
}
.docker_menu_button {
  display: inline-block;
  width: 100%;
  padding: 16px 0 18px;
  vertical-align: top;
  box-sizing: border-box;
}
.my_page .docker_menu_button {
  overflow: hidden;
  display: inline-block;
  width: 61px;
  height: 61px;
  padding: 0;
  margin: 0 23px;
  vertical-align: top;
  border-radius: 100%;
}

.docker_menu_button::after {
  content: "";
  display: block;
  margin: 0 auto;
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: 0 0;
}
.main .docker_menu_button::after {
  width: 28px;
  height: 28px;
  background-size: 28px 28px;
  background-image: url("../img/docker_home.png");
}

.search .docker_menu_button::after {
  width: 28px;
  height: 28px;
  background-size: 28px 28px;
  background-image: url("../img/docker_search.png");
}

.my_page .docker_menu_button::after {
  width: 61px;
